export interface User {
    id:     number,
    name:   string,
    email:  string,
    password: string
}

export class User implements User {
    constructor(
        id: number,
        name: string,
        email: string,
        password: string
    ) {
        this.id         = id;
        this.name       = name;
        this.email      = email;
        this.password   = password;
    }
}